'use client';

import React, { useState } from 'react';
import { useAuth } from '@/components/auth-provider';
import { Gamepad2, ArrowRight, Loader2 } from 'lucide-react';

export default function LoginPage() {
    const { login } = useAuth();
    const [username, setUsername] = useState('');
    const [password, setPassword] = useState('');
    const [error, setError] = useState('');
    const [isSubmitting, setIsSubmitting] = useState(false);

    const handleSubmit = async (e: React.FormEvent) => {
        e.preventDefault();
        setIsSubmitting(true);
        setError('');

        try {
            await login({ username, password });
        } catch (err: any) {
            setError(err.error || 'Login failed');
            setIsSubmitting(false);
        }
    };

    return (
        <div className="min-h-screen bg-[#050a07] flex items-center justify-center p-4">
            {/* Background Decor */}
            <div className="fixed inset-0 overflow-hidden pointer-events-none">
                <div className="absolute top-0 right-0 w-[500px] h-[500px] bg-[#22c55e] rounded-full filter blur-[150px] opacity-5 -translate-y-1/2 translate-x-1/2"></div>
                <div className="absolute bottom-0 left-0 w-[500px] h-[500px] bg-[#22c55e] rounded-full filter blur-[150px] opacity-10 translate-y-1/2 -translate-x-1/2"></div>
            </div>

            <div className="w-full max-w-sm relative z-10">
                <div className="text-center mb-8">
                    <div className="size-16 bg-[#22c55e] rounded-2xl flex items-center justify-center mx-auto mb-6 transform rotate-3 shadow-[0_0_30px_rgba(34,197,94,0.4)]">
                        <Gamepad2 size={40} className="text-black" />
                    </div>
                    <h1 className="text-3xl font-bold text-white tracking-tight mb-2">Billiard<span className="text-[#22c55e]">OS</span></h1>
                    <p className="text-slate-400">Sign in to manage your venue</p>
                </div>

                <form onSubmit={handleSubmit} className="bg-[#0f1a14] border border-[#1e3328] rounded-2xl p-8 space-y-6 shadow-2xl">
                    {error && (
                        <div className="bg-red-500/10 border border-red-500/20 text-red-500 text-sm p-3 rounded-lg text-center">
                            {error}
                        </div>
                    )}

                    <div className="space-y-2">
                        <label className="text-xs font-bold text-slate-400 uppercase tracking-wider">Username</label>
                        <input
                            type="text"
                            value={username}
                            onChange={(e) => setUsername(e.target.value)}
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded-lg px-4 py-3 text-white focus:outline-none focus:border-[#22c55e] focus:ring-1 focus:ring-[#22c55e] transition-all"
                            placeholder="Enter your username"
                            required
                        />
                    </div>

                    <div className="space-y-2">
                        <label className="text-xs font-bold text-slate-400 uppercase tracking-wider">Password</label>
                        <input
                            type="password"
                            value={password}
                            onChange={(e) => setPassword(e.target.value)}
                            className="w-full bg-[#050a07] border border-[#1e3328] rounded-lg px-4 py-3 text-white focus:outline-none focus:border-[#22c55e] focus:ring-1 focus:ring-[#22c55e] transition-all"
                            placeholder="••••••••"
                            required
                        />
                    </div>

                    <button
                        type="submit"
                        disabled={isSubmitting}
                        className="w-full bg-[#22c55e] hover:bg-[#16a34a] text-black font-bold py-3.5 rounded-lg flex items-center justify-center gap-2 transition-all duration-200 transform hover:scale-[1.02] active:scale-[0.98] disabled:opacity-70 disabled:cursor-not-allowed"
                    >
                        {isSubmitting ? <Loader2 size={20} className="animate-spin" /> : <>Sign In <ArrowRight size={20} /></>}
                    </button>
                </form>

                <p className="text-center text-slate-600 text-xs mt-8">
                    &copy; 2025 BilliardOS Management System
                </p>
            </div>
        </div>
    );
}
